import React, { Component } from 'react';

import {
  Card,
  Row,
  Col,
  InputNumber,
  Form,
  message,
} from 'antd';
import { CartHeadAction } from 'components/common/index';
import BtnExport from 'components/common/button/BtnExport';
import { isEmpty } from 'lodash';

import { TableDataEditCell } from 'components/common';
import { BtnSave } from 'components/common/button/index';
import { requiredRule } from 'utils/validateRules';
import Page from 'components/LayoutComponents/Page';
import { getQueryParams } from 'utils/urlUtil';
import { getClassOwnerActivitys, saveClassOwnerActivity, exportClassOwnerActivities } from 'services/classOwnerActivity';
import SearchBar from './SearchBar';

import './style.scss';


class ClassOwnerActivity extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
      loading: false,
      paramSearch: {},
      updatedClasses: {},
      enableExport: false
    };
    this.columns = [
      {
        title: 'Khóa',
        dataIndex: 'courseLabel',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'studyFieldLabel',
      },
      {
        title: 'Năm học',
        dataIndex: 'yearEduLabel',
      },
      {
        title: 'Học kỳ',
        dataIndex: 'semesterLabel',
      },
      {
        title: 'Lớp',
        dataIndex: 'classLabel',
      },
      {
        title: 'Tên giáo viên',
        dataIndex: 'teacherName',
      },
      {
        title: 'Số ngày',
        dataIndex: 'noOfDay',
        key: 'noOfDay',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.key}.noOfDay`, {
              initialValue: record.noOfDay,
              rules: [requiredRule],
            })(<InputNumber onPressEnter={onSave} onBlur={onSave} ref={setRefFormItem} />)}
          </Form.Item>
        ),
      },
    ];
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleExport = async() => {
    try {
      await exportClassOwnerActivities(this.state.paramSearch);
      message.success('Xuất dữ liệu thành công');
    } catch(err) {
      message.error('Có lỗi xãy ra!');
    }
  }

  handleSave = async() => {
    const { updatedClasses } = this.state;
    try {
      if(!isEmpty(updatedClasses)) {
        const classes = Object.entries(updatedClasses).map(classEntry => ({
          classId: classEntry[1].classId,
          teacherId: classEntry[1].teacherId,
          yearEduId: classEntry[1].yearEduId,
          semesterId: classEntry[1].semesterId,
          noOfDay: classEntry[1].noOfDay,
        }));
        await saveClassOwnerActivity(classes);
        message.success('Cập nhật thành công');
        console.log(classes);
      }
    } catch(error) {
      message.error('Có lỗi xãy ra');
    }
  }

  handleSaveCellData = rowData => {
    const { updatedClasses } = this.state
    const identifyKey = `${rowData.classId}_${rowData.teacherId}_${rowData.yearEduId}_${rowData.semesterId}`;
    updatedClasses[identifyKey] = rowData;
    this.setState({ updatedClasses });
    console.log(updatedClasses);
  }

  render() {
    const { loading, enableExport, paramSearch: { trainTypeId, facultyId, courseId, studyFieldId } } = this.state;
    
    return (
      <Card title='Khối lượng công việc cố vấn / chủ nhiệm'>
        <Row>
          <CartHeadAction>
            <BtnExport title="Xuất dữ liệu" disabled={!enableExport} onClick={this.handleExport} />
          </CartHeadAction>
        </Row>
        <Row gutter={16}>
          <Col span={24}>
            <SearchBar />
          </Col>
        </Row>
        <Row>
          <Col>
            <BtnSave onClick={this.handleSave} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableDataEditCell
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={async(params) => {
                const result = await getClassOwnerActivitys(params);
                this.setState({ enableExport: result.data && !isEmpty(result.data.items) })
                return result;
              }}
              columns={this.columns}
              loading={loading}
              handleSaveCellData={this.handleSaveCellData}
              allowGetData={!!trainTypeId && !!facultyId && !!courseId && !!studyFieldId}
              rowKey="key" 
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassOwnerActivity />
  </Page>
);



